.btn {
  width: 190px;
  height: 40px;
  background-color: #fff;
  border: 2px solid #2a2a72;
  color: #2a2a72;
  outline: none;
  cursor: pointer;
  font-size: 20px;
  font-family: "Poppins", sans-serif;
  text-transform: capitalize;
  text-align: center;
  position: relative;
  z-index: 3;
  transition: all 0.5s;
}
@media screen and (min-width: 992px) {
  .btn::before {
    content: "";
    width: calc(100% + 12px);
    height: calc(100% - 12px);
    position: absolute;
    top: 6px;
    left: -6px;
    background-color: #fff;
    z-index: -1;
    transition: all 0.5s ease-in-out;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  .btn::after {
    content: "";
    width: calc(100% - 12px);
    height: calc(100% + 12px);
    position: absolute;
    top: -6px;
    left: 6px;
    background-color: #fff;
    z-index: -1;
    transition: all 0.5s ease-in-out;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }

  .btn:hover.btn::before {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }
  .btn:hover.btn::after {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  .clear_btn {
    margin-top: 40px;
  }
}
